
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import HeadVue from './head.vue'
import fetch from '../config/fetch'
var fb = require('querystring')
export default{
data(){
return{
entName:'',
address:'',
person:'',
phone:'',
createTime:'',
feedback:'',
description:''
}
},
methods:{
finish: function () {
if (!this.feedback) {
this.$vux.toast.text('反馈内容为空！', 'middle')
return
}
let params = {
"taskId": this.$store.state.taskId,
"solution": this.feedback,
}
fetch('/api/yw/updateWorkOrderItemTask',fb.stringify(params),'PUT')
.then(res =>{
if(res.code == 0){
this.$vux.toast.text("反馈成功", 'middle')
}else{
this.$vux.toast.text(res.msg, 'middle')
}

}).catch(error =>{

this.$vux.toast.text('出现错误,请重新登录', 'middle')
this.$router.push('/');
})
}
},
components:{
HeadVue,
XButton
},
mounted(){
let taskId = this.$store.state.taskId;
fetch('/api/yw/entDetail?taskId='+taskId,{},'GET')
.then(res =>{
if(res.code == 0){
this.entName = res.data.entName;
this.address = res.data.address;
this.person = res.data.person;
this.phone = res.data.phone;
this.createTime = res.data.createTime
}else{
this.$vux.toast.text(res.msg, 'middle')
}
}).catch(error =>{
this.$vux.toast.text('出现错误,请重新登录', 'middle')
})

fetch('/api/yw/workOrderDetail?taskId='+taskId,{},'GET')
.then(res =>{
if(res.code == 0){
this.description = res.data.description;
}else{
this.$vux.toast.text(res.msg, 'middle')
}
}).catch(error =>{
this.$vux.toast.text('出现错误,请重新登录', 'middle')
})

}
}



