import axios from "axios";
import HeadVue from './head.vue'
  export default {
      name: "signature",
    data(){
      return{
        points:[],
        canvasTxt:null,
        startX:0,
        startY:0,
        moveY:0,
        moveX:0,
        endY:0,
        endX:0,
        w:null,
        h:null,
        isDown:false
      }
    },
    created(){

    },
    mounted(){
      let canvas=this.$refs.canvasF;
      canvas.height = this.$refs.canvasHW.offsetHeight - 60;
      canvas.width =this.$refs.canvasHW.offsetWidth-10;
      this.canvasTxt=canvas.getContext("2d");
    },
    components:{
      // NavPublic
        HeadVue,
    },
    methods:{
      backHome(){
        window.history.back();
      },
      //电脑设备事件
      mouseDown(ev){
        ev = ev || event;
        ev.preventDefault();
        console.log(ev);
        if(1){
          let obj={
            x:ev.offsetX,
            y:ev.offsetY
          };
          console.log(obj);
          this.startX=obj.x;
          this.startY=obj.y;
          this.canvasTxt.beginPath();
          this.canvasTxt.moveTo(this.startX, this.startY);
          this.canvasTxt.lineTo(obj.x, obj.y);
          this.canvasTxt.stroke();
          this.canvasTxt.closePath();
          this.points.push(obj);
          this.isDown=true;
        }
      },
      //移动设备事件
      touchStart(ev){
        ev = ev || event;
        ev.preventDefault();
        if(ev.touches.length == 1) {
          let obj={
            x:ev.targetTouches[0].clientX,
            y:ev.targetTouches[0].clientY-48
          };
          this.startX=obj.x;
          this.startY=obj.y;
          this.canvasTxt.beginPath();
          this.canvasTxt.moveTo(this.startX, this.startY);
          this.canvasTxt.lineTo(obj.x, obj.y);
          this.canvasTxt.stroke();
          this.canvasTxt.closePath();
          this.points.push(obj);
        }
      },
      //电脑设备事件
      mouseMove(ev){
        ev = ev || event;
        ev.preventDefault();
        if(this.isDown){
          let obj={
            x:ev.offsetX,
            y:ev.offsetY
          };
          this.moveY=obj.y;
          this.moveX=obj.x;
          this.canvasTxt.beginPath();
          this.canvasTxt.moveTo(this.startX, this.startY);
          this.canvasTxt.lineTo(obj.x, obj.y);
          this.canvasTxt.stroke();
          this.canvasTxt.closePath();
          this.startY=obj.y;
          this.startX=obj.x;
          this.points.push(obj);
        }
      },
      //移动设备事件
      touchMove(ev){
        ev = ev || event;
        ev.preventDefault();
        if(ev.touches.length == 1) {
          let obj={
            x:ev.targetTouches[0].clientX,
            y:ev.targetTouches[0].clientY-48
          };
          this.moveY=obj.y;
          this.moveX=obj.x;
          this.canvasTxt.beginPath();
          this.canvasTxt.moveTo(this.startX, this.startY);
          this.canvasTxt.lineTo(obj.x, obj.y);
          this.canvasTxt.stroke();
          this.canvasTxt.closePath();
          this.startY=obj.y;
          this.startX=obj.x;
          this.points.push(obj);
        }
      },
      //电脑设备事件
      mouseUp(ev){
        ev = ev || event;
        ev.preventDefault();
        if(1){
          let obj={
            x:ev.offsetX,
            y:ev.offsetY
          };
          this.canvasTxt.beginPath();
          this.canvasTxt.moveTo(this.startX, this.startY);
          this.canvasTxt.lineTo(obj.x, obj.y);
          this.canvasTxt.stroke();
          this.canvasTxt.closePath();
          this.points.push(obj);
          this.points.push({x:-1,y:-1});
          this.isDown=false;
        }
      },
      //移动设备事件
      touchEnd(ev){
        ev = ev || event;
        ev.preventDefault();
        console.log(ev);
        if(ev.touches.length == 1) {
          let obj={
            x:ev.targetTouches[0].clientX,
            y:ev.targetTouches[0].clientY-48
          };
          this.canvasTxt.beginPath();
          this.canvasTxt.moveTo(this.startX, this.startY);
          this.canvasTxt.lineTo(obj.x, obj.y);
          this.canvasTxt.stroke();
          this.canvasTxt.closePath();
          this.points.push(obj);
          this.points.push({x:-1,y:-1});
        }
      },
      //重写
      overwrite(){
        this.canvasTxt.clearRect(0, 0, this.$refs.canvasF.width, this.$refs.canvasF.height);
        this.points=[];
      }
    }
  }



