
import HeadVue from './head.vue'
import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VArea from 'vux/src/components/v-chart/v-area.vue'
import VBar from 'vux/src/components/v-chart/v-bar.vue'
import VPie from 'vux/src/components/v-chart/v-pie.vue'
import VPoint from 'vux/src/components/v-chart/v-point.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VAxis from 'vux/src/components/v-chart/v-axis.vue'
import VGuide from 'vux/src/components/v-chart/v-guide.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VLegend from 'vux/src/components/v-chart/v-legend.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'











const data = [
{ name: '处理中', percent: 30, a: '1' },
{ name: '未处理', percent: 50, a: '1' },
{ name: 'enen', percent: 10, a: '1' },
{ name: 'ddddd', percent: 10, a: '1' },
]
const pieData=[
{month:"1月" , sales:63},
{month:"2月" , sales:46},
{month:"3月" , sales:55},
{month:"4月" , sales:96},
{month:"5月" , sales:60},
{month:"6月" , sales:28}
]
const map = {}
data.map(obj => {
map[obj.name] = obj.percent + '%'
})


export default{
data(){
return{
map,
legendOptions: {
position: 'bottom',
itemFormatter (val) {
return val + '  ' + map[val]
}
},
data,
pieData,
value1: '2018-10',
progressDay:{
width:'90%'
},
progressWeek:{
width:'60%'
},
progressMonth:{
width:'30%'
},
isSixDay :false,
isThreeDay :false,
isZeroDay :false,
isSixWeek :false,
isThreeWeek :false,
isZeroWeek :false,
isSixMonth :false,
isThreeMonth :false,
isZeroMonth :false,
}
},
components:{
HeadVue,
VChart,
VLine,
VArea,
VBar,
VPie,
VPoint,
VScale,
VAxis,
VGuide,
VTooltip,
VLegend,
XProgress,
Box,
Datetime,
Group
},
methods:{
change (value) {
console.log('change', value)
},
onConfirm (val) {
console.log('on-confirm arg', val)
console.log('current value', this.value1)
},
log (str1, str2 = '') {
console.log(str1, str2)
},
myFormat(){
this.day = ""
}
},
mounted:function (){

if(this.progressDay.width > '60%'){
this.isSixDay = true;
this.isThreeDay = false;
this.isZeroDay = false;
}
if(this.progressDay.width <= '60%' && this.progressDay.width > '30%'){
this.isThreeDay = true;
this.isSixDay = false;
this.isZeroDay = false;
}
if(this.progressDay.width <= '30%'){
this.isZeroDay = true;
this.isThreeDay = false;
this.isSixDay = false;
}

if(this.progressWeek.width > '60%'){
this.isSixWeek = true;
this.isThreeWeek = false;
this.isZeroWeek = false;
}
if(this.progressWeek.width <= '60%' && this.progressWeek.width > '30%'){
this.isThreeWeek = true;
this.isSixWeek = false;
this.isZeroWeek = false;
}
if(this.progressWeek.width <= '30%'){
this.isZeroWeek = true;
this.isThreeWeek = false;
this.isSixWeek = false;
}

if(this.progressMonth.width > '60%'){
this.isSixMonth = true;
this.isThreeMonth = false;
this.isZeroMonth = false;
}
if(this.progressMonth.width <= '60%' && this.progressMonth.width > '30%'){
this.isThreeMonth = true;
this.isSixMonth = false;
this.isZeroMonth = false;
}
if(this.progressMonth.width <= '30%'){
this.isZeroMonth = true;
this.isThreeMonth = false;
this.isSixMonth = false;
}
}
}



