
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'HelloWorld',
data () {
return {
msg: 'Welcome'
}
},
mounted () {

},
components: {
Group,
Cell
},
methods: {

jump: function () {
this.msg = this.$store.state.count
this.$router.push('test')
}
},
created: function () {
var self = this
self.msg = this.$store.state.latitude
}
}



