
import Alert from 'vux/src/components/alert/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import HeadVue from './head.vue'
import leftDelete from './leftDelete.vue'
import fetch from '../config/fetch'
export default{
data(){
return{

list:[],

isXun:false,
isGong:false,
isYing:true,
isPor:false,
untreated:true,
processed :false,
timeOutEvent: 0 ,   //计时器
checkOne :true,
showCheck :false,
allOrno :'全选',
removeStyle:['test'],
labList:[
{
name:"巡检"
}
],
lists:[
{
fon:false,
name:1111
},
{
fon:false,
name:221221
},
{
fon:false,
name:287877798
},
{
fon:false,
name:2222
},
{
fon:false,
name:2854321
}
]
}
},
components:{
Alert,
Tab,
TabItem,
Badge,
LoadMore,
HeadVue,
CheckIcon,
leftDelete
},
methods:{
goPath(path,entId,entName){
this.$store.commit('setEntId',entId)
this.$store.commit('setEntName',entName)
this.$router.push({path:path})
},









processedClick(){

this.isPor = true;
this.isXun = false;
this.isGong = false;
this.isYing = false;
this.processed = true;
this.untreated = false;
},

gtouchstart(i){
var that = this;
this.timeOutEvent = setTimeout(function () {
that.showCheck = true;
that.lists[i].fon = true; 

},600);
return false
},

gtouchend(){
clearTimeout(this.timeOutEvent)
return false
},

gtouchmove(){
clearTimeout(this.timeOutEvent)
this.timeOutEvent = 0
this.showCheck = false;

for(var x=0;x<this.lists.length;x++){
this.lists[x].fon = false;
}
},






_deleteClick(index){

var self =this;
self.lists.splice(index,1);
},

liClick(list){


if(this.showCheck == true){
list.fon = !list.fon
}else{

}



},

cancelShow(){
var that = this;
that.removeStyle = 'test1';  
setTimeout(function(){
that.showCheck = false;
that.removeStyle = 'test';
},300)

for(var x=0;x<this.lists.length;x++){
this.lists[x].fon = false;
}
},

selectAll(){
if(this.allOrno =='全选'){
for(var i=0;i<this.lists.length;i++){
this.lists[i].fon = true;
}
this.allOrno = '全不选'
}else{
for(var d=0;d<this.lists.length;d++){
this.lists[d].fon = false;
}
this.allOrno = '全选'
}
},

removeAll(){
var zun = [];
for(var z=0;z<this.lists.length;z++){
if(this.lists[z].fon==true){  // 0 2 4
zun.push(z);    
}
}
console.log(zun)
for(var q=zun.length-1;q>=0;q--){     //因为splice会影响数组索引，所致从索引大的开始删除
this.lists.splice(zun[q],1);
}

if(zun.length==0){
this.$vux.toast.text('请勾选要删除', 'middle')
}else{
this.showCheck = false;
}
}
},
mounted(){
fetch('/api/yw/list',{},'GET')
.then(res =>{
if(res.code == 1000){
this.$vux.toast.text('登录失效请重新登录', 'middle')
this.$router.push('/');
}else {
this.list = res.data;




}
}).catch(error =>{

this.$vux.toast.text('出现错误,请重新登录', 'middle')
this.$router.push('/');
})
}
}




