/**
 * 配置编译环境和线上环境之间的切换
 * baseUrl: 域名地址
 * routerMode: 路由模式
 * imgBaseUrl: 图片所在域名地址
 */

let baseUrl = ''
let routerMode = 'hash'
let imgBaseUrl = ''
// console.log("sss")
if (process.env.NODE_ENV === 'development') {
  imgBaseUrl = '/img/'
  baseUrl = ''
} else if (process.env.NODE_ENV === 'production') {
  baseUrl = 'http://39.106.188.126:9192'
  imgBaseUrl = 'www.igreenep.cn'
}

export {
  baseUrl,
  routerMode,
  imgBaseUrl
}



