
import Alert from 'vux/src/components/alert/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'

import HeadVue from './head.vue'
export default {
name: 'location',
data () {
let self = this;
return {
msg: '00C1DE',
password: '',
username: '',

zoom :14,
center: [this.$store.state.gaodePosition[0],this.$store.state.gaodePosition[1]],

gaodePosition: [this.$store.state.gaodePosition[0],this.$store.state.gaodePosition[1]],
plugin: [{
pName: 'Geolocation',
buttonPosition: 'RB',
events: {
init(o) {

o.getCurrentPosition((status, result) => {
if (result && result.position) {
self.lng = self.gaodePosition[0];
self.lat = self.gaodePosition[1];
self.center = [self.lng, self.lat]; 


self.$nextTick();
}
});
}
}
}],
markers: [

{
center: [self.lng, self.lat],
},

],
markerEvents: {
click: () =>{
alert("click")
}
}
}
},
mounted () {

},
components: {
Alert,
HeadVue,
Tab,
TabItem,
Badge
},
methods: {
handler: function (argument) {

},
baiduMap () {

},
positionClick(){
this.center = [this.$store.state.gaodePosition[0],this.$store.state.gaodePosition[1]];
this.zoom = 14;

}

},
created: function () {
}
}



