import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import test from '@/components/test'
import login from '@/components/login'
import home from '@/components/home'
import bulter from '@/components/bulter'
import arrange from '@/components/arrange'
import analy from '@/components/analy'
import my from '@/components/my'
import enterPosition from '@/components/enterPosition'
import mochaItom from '@/components/mochaItom'
import stateOfInves from '@/components/stateOfInves'
import checkPro from '@/components/checkPro'
import emerManage from '@/components/emerManage'
import replaceConsum from '@/components/replaceConsum'
import instNorm from '@/components/instNorm'
import equiInfo from '@/components/equiInfo'
import deteInfo from '@/components/deteInfo'
import emission from '@/components/emission'
import enterInfo from '@/components/enterInfo'
import mainInfo from '@/components/mainInfo'
import mainItem from '@/components/mainItem'
import deteItem from '@/components/deteItem'
import notification from '@/components/notification'
import emergency from '@/components/emergency'
import monitoring from '@/components/monitoring'
import monitorInfor from '@/components/monitorInfor'
import workList from '@/components/workList'
import worklistContent from '@/components/worklistContent'
import myBusiness from '@/components/myBusiness'
import listOfCompany  from '@/components/listOfCompany'
import inspection from '@/components/inspection'
import carbon from '@/components/carbon'
import equipment from '@/components/equipment'
import vocPort from '@/components/vocPort'
import hidden from '@/components/hidden'
import wacom from '@/components/wacom'
import location from '@/components/location'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/login',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/',
      name: 'login',
      meta: {allowBack: false},
      component: login
    },
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/home',
      // name: 'home',
      component: home,
      children: [
        {
          path: '',
          redirect: '/home/bulter',
          component: my
        },
        {
          path: 'my',
          name: 'my',
          component: my
        },
        {
          path: 'bulter',
          name: 'bulter',
          // meta:{
          //   keepAlive:true//
          // },
          component: bulter
        },
        {
          path: 'arrange',
          name: 'arrange',
          component: arrange
        },
        {
          path: 'analy',
          name: 'analy',
          component: analy
        }
      ]
    },
    // 企业地图
    {
      path: '/enterPosition',
      name: 'enterPosition',
      component: enterPosition
    },
    // 运维管理
    {
      path: '/mochaItom',
      name: 'mochaItom',
      component: mochaItom
    },
    // 排除隐患
    {
      path: '/stateOfInves',
      name: 'stateOfInves',
      component: stateOfInves
    },
    // 检测校对
    {
      path: '/checkPro',
      name: 'checkPro',
      component: checkPro
    },
    // 应急处理
    {
      path: '/emerManage',
      name: 'emerManage',
      component: emerManage
    },
    // 耗材更换
    {
      path: '/replaceConsum',
      name: 'replaceConsum',
      component: replaceConsum
    },
    // 制度规范
    {
      path: '/instNorm',
      name: 'instNorm',
      component: instNorm
    },
    // 设备信息
    {
      path: '/equiInfo',
      name: 'equiInfo',
      component: equiInfo
    },
    // 检测信息
    {
      path: '/deteInfo',
      name: 'deteInfo',
      component: deteInfo
    },
    // 排放指标
    {
      path: '/emission',
      name: 'emission',
      component: emission
    },
    // 企业信息
    {
      path: '/enterInfo',
      name: 'enterInfo',
      component: enterInfo
    },
    // 企业运维信息
    {
      path: '/mainInfo',
      name: 'mainInfo',
      component: mainInfo
    },
    // 环保设备完整性
    {
      path: '/mainItem',
      name: 'mainItem',
      component: mainItem
    },
    // 检测设备信息
    {
      path: '/deteItem',
      name: 'deteItem',
      component: deteItem
    },
    // 任务通知
    {
      path:'/notification',
      name:'notification',
      component:notification
    },
    // 应急内容
    {
      path:'/emergency',
      name:'emergency',
      component:emergency
    },
    // 监控报警
    {
      path:'/monitoring',
      name:'monitoring',
      component:monitoring
    },
    // 监控信息
    {
      path:'/monitorInfor',
      name:'monitorInfor',
      component:monitorInfor
    },
    // 工单
    {
      path:'/workList',
      name:'workList',
      component:workList
    },
    // 工单内容
    {
      path:'/worklistContent',
      name:'worklistContent',
      component:worklistContent
    },
    // 我的业务
    {
      path:'/myBusiness',
      name:'myBusiness',
      component:myBusiness
    },
    // 公司巡检列表
    {
      path:'/listOfCompany',
      name:'listOfCompany',
      component:listOfCompany
    },
    // 设备巡检
    {
      path:'/inspection',
      name:'inspection',
      component:inspection
    },
    // 设备巡检列表
    {
      path:'/carbon',
      name:'carbon',
      component:carbon
    },
    // 检测设备
    {
      path:'/equipment',
      name:'equipment',
      component:equipment
    },
    // voc 检测口页面
    {
      path:'/vocPort',
      name:'vocPort',
      component:vocPort
    },
    // 隐患排查
    {
      path:'/hidden',
      name:'hidden',
      component:hidden
    },
    //手写板
    {
      path:'/wacom',
      name:'wacom',
      component:wacom
    },
    //所在位置
    {
      path:'/location',
      name:'location',
      component:location
    }   
  ],
})



