import HeadVue from './head.vue';
import fetch from '../config/fetch'
export default {
  data () {
    return {
      scrool:'',
      inspectCount:'0',
      urgentCount:'0',
      workOrderCount:'0',
      total:'0'
    }
  },
  components: {
  	HeadVue
  },
  methods: {
    goPath: function (path) {
      // body...
      this.$router.push({path: path})
    }
  },
  mounted (){
    //页面加载数据
      fetch('/api/yw/count',{},'GET')
          .then(res =>{
            if(res.code == 1000){ 
          this.$vux.toast.text('登录失效请重新登录', 'middle')
              this.$router.push('/');   
            }else {
              this.inspectCount = res.data.inspectCount;
              this.urgentCount = res.data.urgentCount;
              this.workOrderCount = res.data.workOrderCount;
              this.total = res.data.total;
            }
          }).catch(error =>{
            // console.log(error)
            this.$vux.toast.text('出现错误', 'middle')
          })

    // 从state获取滚动高度
      let positionBulter = this.$store.state.positionBulter //返回页面取出来
      console.log("positionBulter取值:"+positionBulter)
      document.querySelector('#positionBulter').scrollTop = positionBulter;

      // 测试fetch
      // this.$http.get('/street/getStreetList/0').then(rep =>{
      //   console.log(rep.data)
      // })
  },
  // 离开路由记录当前滚动位置存到state里
  beforeRouteLeave(to, from, next){
    let positionBulter =document.querySelector('#positionBulter').scrollTop;
    this.$store.commit('positionBulter', positionBulter) //离开路由时把位置存起来
    next()
  },
}



