

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


import 'vue-event-calendar/dist/style.css'
import Promise from "promise-polyfill"


import AMap from 'vue-amap'
import gcoord from 'gcoord'




import Mint from 'mint-ui'

import 'lib-flexible'
if(!window.Promise){
window.Promise = Promise;
}




Vue.use(Mint);
Vue.use(gcoord);





















Vue.use(ToastPlugin)

Vue.use(AMap)
AMap.initAMapApiLoader({
key:'35de5cb6f8ceef6f8a278c9871da52ce',
plugin:['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor','AMap.Geolocation'],

})




























const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
endTime = Date.now()
})
methods.forEach(key => {
let method = router[key].bind(router)
router[key] = function (...args) {
isPush = true
method.apply(null, args)
}
})
var firstBack = null

router.beforeEach(function (to, from, next) {







const toIndex = history.getItem(to.path)
const fromIndex = history.getItem(from.path)

if(to.name=='login' && from.name=='bulter' || to.name=='login' && from.name=='analy' || to.name=='login' && from.name=='user'|| to.name=='login' && from.name=='arrange'){


if(!firstBack){


firstBack =1;
Vue.$vux.toast.text('再点一次退出应用', 'bottom');

setTimeout(function(){
firstBack = '';

},1000)
next(false);
return;
}else{


firstBack = '';
navigator.app.exitApp();

}
}




if (toIndex) {
if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
store.commit('updateDirection', {direction: 'slide-left'})
} else {

if (!isPush && (Date.now() - endTime) < 377) {
store.commit('updateDirection', {direction: ''})
} else {
store.commit('updateDirection', { direction: 'slide-right' })
}
}
} else {
++historyCount
if (toIndex === 0) {
return
}
history.setItem('count', historyCount)
to.path !== '/' && history.setItem(to.path, historyCount)
store.commit('updateDirection', {direction: 'slide-left'})
}

if (/\/http/.test(to.path)) {
let url = to.path.split('http')[1]
window.location.href = `http${url}`
} else {
next()
}
})




if(navigator.geolocation){

navigator.geolocation.getCurrentPosition(function(position){
var lat = position.coords.latitude;
var lng = position.coords.longitude;

var gaodePosition = gcoord.transform(
[lng,lat],
gcoord.WGS84,               // 当前坐标系
gcoord.GCJ02                 
)

store.commit("setGaodePosition",gaodePosition);
},function(error){



},{ maximumAge: 3000, enableHighAccuracy: true })
}else{
alert("不支持");
}

















/*
*判断是否登录过，获取账号，如果有直接进入home
*否则重新登录
*/










Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})



