
import Alert from 'vux/src/components/alert/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import HeadVue from './head.vue'
import myBusiness from './myBusiness'
import fetch from '../config/fetch'
export default{
name: 'listOfCompany',
data(){
return{
tasklist:[],
isNormal:false,
isWorkOrder:false,
taskOutLevel:'0',
status:'PROCESSING',
companyName:this.$store.state.entName
}
},
components:{
Alert,
Tab,
TabItem,
Badge,
LoadMore,
HeadVue,
myBusiness
},
methods:{

goPath(path,taskId){
this.$store.commit('setTaskId',taskId);
this.$router.push({path:path});
},


},
mounted(){
let entId = this.$store.state.entId;
fetch('/api/yw/taskList?entId='+entId,{},'GET')
.then(res =>{
if(res.code == 0){
this.tasklist = res.data[0];
}else{
this.$vux.toast.text(res.msg, 'middle')
}
}).catch(error =>{
this.$vux.toast.text('出现错误,请重新登录', 'middle')
})
}
}




