
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import fetch from '../config/fetch'
var qs = require('querystring')
export default {
name: 'login',
data () {
return {
msg: '00C1DE',
password: '',
username: ''
}
},
mounted () {

},
components: {
XButton,
Alert
},
methods: {

login: function () {

if (!this.username) {
this.$vux.toast.text('用户名为空！', 'middle')
return
}
if (!this.password) {
this.$vux.toast.text('密码为空！', 'middle')
return
}


















let params = {
"username": this.username,
"password": this.password,



}

fetch('/login',qs.stringify(params),'POST')
.then(res =>{
if(res.code == 0){
if (res.data.role == "YUN_WEI") {
this.$router.push('/home');
let userInfo = params;

this.$store.commit('setUserInfo',userInfo)
this.$vux.toast.text("登录成功", 'middle')
}else {
this.$vux.toast.text("非运维人员账号", 'middle')
}
}else{
this.$vux.toast.text(res.msg, 'middle')
}

}).catch(error =>{

this.$vux.toast.text('出现错误,请重新登录', 'middle')
this.$router.push('/');
})







}
},

created: function () {

}
}



