
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import HeadVue from './head.vue'
export default {
data () {
return {
msg: 'Welcome',
liOne: false,
liOneText: '展开全部',
liTwo: false,
liTwoText: '展开全部'
}
},
mounted () {

},
components: {
Group,
Cell,
HeadVue
},
methods: {

expand: function (num) {
if (num === 1) {
this.liOne = !this.liOne
if (this.liOne) {
this.liOneText = '点击回收'
} else {
this.liOneText = '展开全部'
}
} else {
this.liTwo = !this.liTwo
if (this.liTwo) {
this.liTwoText = '点击回收'
} else {
this.liTwoText = '展开全部'
}
}
}
},
created: function () {


}
}



