import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex)

const state = {
  isTure: 'true', // 当前位置纬度
  direction: '' ,// 左滑右滑动画  slide-right slide-left slide-reverse(ios左滑)
  positionBulter : 0,
  positionEmer:0,
  positionMon:0,
  userInfo: localStorage.getItem('userInfo'),
  gaodePosition:''
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



