import HeadVue from './head.vue';
import fetch from '../config/fetch'
export default {
  data () {
    return {
    	name: '',
    	roleName:'',
    	orgPhone:'',
    	ywsn:'',
		avatar:''
    }
  },
  components: {
    HeadVue
  },
  mounted(){
    //页面加载数据
  	  fetch('/api/self',{},'GET')
      .then(res =>{
        if(res.code == 1000){ 
			this.$vux.toast.text('登录失效请重新登录', 'middle')
        	this.$router.push('/');		
        }else {
        	this.name = res.data.name;
        	this.roleName = res.data.roleName;
        	this.orgPhone = res.data.orgPhone;
        	this.ywsn = res.data.ywSn;
        	this.avatar = res.data.avatar;
        	if (res.data.ywSn == '') {
        		this.ywsn = '00001';
        	}
        }
      }).catch(error =>{
        // console.log(error)
        this.$vux.toast.text('出现错误,请重新登录', 'middle')
        this.$router.push('/');
      })
  },
  methods:{
  	exitBtn:function(path) {
  		// body...
  		this.$router.push({path:path});
  		localStorage.removeItem('userInfo')
  	},
  	goPath: function (path) {
      // body...
      this.$router.push({path: path})
    }
  }
}



