
import Alert from 'vux/src/components/alert/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import HeadVue from './head.vue'
import BScroll from 'better-scroll'
export default {
name: 'mochaItom',
data () {
return {
msg: '00C1DE',
password: '',
username: '',
data: ['000', '111', '222', '333'],
laodingMore: true,
showLoad: false,
tip: '正在加载'
}
},
mounted () {
this.loadData()
},
components: {
Alert,
HeadVue,
Tab,
TabItem,
Badge,
LoadMore
},
methods: {

handler: function (argument) {

},
loadData () {
this.$nextTick(() => {
if (!this.scroll) {
this.scroll = new BScroll(this.$refs.wrapper, {
pullUpLoad: true
})
this.scroll.on('touchEnd', (pos) => {

if (pos.y > 50) {
console.log('下拉刷新成功')

}

if (this.scroll.maxScrollY > pos.y + 10) {












}
})




} else {
this.scroll.refresh()
}
})
}
},
created: function () {
this.loadData()
}
}



